<template>
  <div id="messageBox">
    <div id="messageContainer">
      <div id="message">
        <div id="header">
          <div id="avatar"></div>
          <input
            class="nick"
            v-model.trim="form.nick"
            type="text"
            placeholder=" ÊòµÁß∞ÔºàÂøÖÂ°´Ôºâ"
          />
          <input
            class="nick"
            v-model.trim="form.email"
            type="text"
            placeholder=" ÈÇÆÁÆ±ÔºàÈÄâÂ°´Ôºâ"
          />
        </div>
        <textarea
          id="textarea"
          ref="textarea"
          placeholder=" ËØ∑ËæìÂÖ•ËæìÂÖ•ÁïôË®Ä"
        ></textarea>
      </div>
      <div id="foot">
        <div
          id="emoji"
          class="iconfont icon-zhayan"
          @click.capture.stop="AddEmoji"
        ></div>
        <div id="submitButton">
          <button ref="center" type="primary" size="medium" @click="OnSubmit">
            ÂèëË°®ËØÑËÆ∫
          </button>
        </div>
        <div id="emojiArr" v-show="emojiTurn">
          <span
            v-for="(item, index) in emoji"
            @click.stop="InsertEmoji(item)"
            :key="index"
            >{{ item }}</span
          >
        </div>
        <div ref="prompt" id="messagePrompt">{{ messagePrompt }}</div>
      </div>
    </div>
    <div id="messageListContainer" v-if="isRefresh">
      <div>ÂÖ®ÈÉ®ËØÑËÆ∫ {{ messageList.length }} Êù°</div>
      <div id="messageItem" v-for="item in messageList" :key="item._id">
        <div id="messageAvatar">
          <img :src="item.avatar" />
        </div>
        <div id="messageItemContent">
          <div id="messageItemHeader">
            <div>{{ item.nick }}</div>
            <div>{{ item.time }}</div>
          </div>
          <div id="messageContent">
            {{ item.content }}
          </div>
          <!-- ÂõûÂ§çÁöÑÂÜÖÂÆπ -->
          <div v-if="item.reply">
            <div
              id="replyItem"
              v-for="replyItem in item.reply"
              :key="replyItem._id"
            >
              <div id="messageAvatar">
                <img src="../assets/logo.jpg" />
              </div>
              <div id="messageItemContent">
                <div id="messageItemHeader">
                  <div>{{ replyItem.nick }}</div>
                  <div>{{ replyItem.time }}</div>
                </div>
                <div id="messageContent">
                  {{ replyItem.content }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import emoji from "../utils/emoji";
export default {
  data() {
    return {
      emoji: emoji,
      emojiTurn: false,
      form: {
        nick: "",
        avatar:
          "https://myvuepressblog-image.oss-cn-shenzhen.aliyuncs.com/avatar2.jpg",
        content: "",
        email: "",
        time: "",
      },
      messageList: [],
      messagePrompt: "ËØÑËÆ∫ÊàêÂäü",
      messagePromptTimer: null,
      isRefresh: true,
    };
  },
  computed: {
    textarea() {
      return this.$refs.textarea;
    },
  },
  methods: {
    InsertEmoji(emoji) {
      // ++++++++++++++++++++++++++++++++++
      let start = this.textarea.selectionStart;
      let end = this.textarea.selectionEnd;
      if (start !== end) {
        let content2 = this.textarea.value;
        let deleteString = this.textarea.value.substring(start, end);
        this.textarea.value = content2.replace(deleteString, "");
        //Â∞Ü‰πãÂâçÁöÑendÂÖâÊ†áËÆæÂíåStartÂÖâÊ†á‰∏ÄËá¥
      }
      let content3 = this.textarea.value;
      this.textarea.value = `${content3.slice(
        0,
        start
      )}${emoji}${content3.slice(start)}`;

      //ÊèíÂÖ•Ë°®ÊÉÖ‰πãÂêéÂ∞ÜÂÖâÊ†áÁßªÂà∞Ë°®ÊÉÖÁöÑÂêéÈù¢Âπ∂‰∏îÂ∞ÜendÂÖâÊ†áÁßªÂà∞ÂíåstartÂÖâÊ†á‰∏ÄËµ∑
      this.textarea.selectionStart = start + 2;
      this.textarea.selectionEnd = this.textarea.selectionStart;
      this.textarea.focus();
    },
    RefreshMessage() {
      this.isRefresh = false;
      this.$nextTick(() => {
        this.isRefresh = true;
      });
    },
    MessagePrompt(message, cssVariable, cssVariableValue) {
      //ÊèêÁ§∫Áõ∏ÂÖ≥
      this.messagePrompt = message;
      this.$refs.prompt.style.setProperty(cssVariable, cssVariableValue);
      clearTimeout(this.messagePromptTimer);
      this.$refs.prompt.classList.add("addPromptAnimation");
      this.messagePromptTimer = setTimeout(() => {
        this.$refs.prompt.classList.remove("addPromptAnimation");
      }, 1000);
    },
    async OnSubmit() {
      this.form.content = this.$refs.textarea.value.trim();
      // Êñ∞ÁïôË®Ä
      if (this.form.nick !== "" && this.form.content !== "") {
        let ePattern =/^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
        if (this.form.email !== "" && !ePattern.test(this.form.email)) {
          this.MessagePrompt("ÈÇÆÁÆ±Ê†ºÂºèÈîôËØØÔºåËØ∑Ê£ÄÊü•", "--color", "red");
          return;
        }
        let result = await this.$http.post("/message", this.form);
        if (result.data) {
          // Âà∑Êñ∞ËØÑËÆ∫
          this.GetMessageList();
          //ÊèêÁ§∫
          this.MessagePrompt("ËØÑËÆ∫ÊàêÂäü", "--color", "skyblue");
          // ÈáçÁΩÆ‰∏∫Á©∫
          this.$refs.textarea.value = "";
          this.form.nick = "";
        }
      } else {
        this.MessagePrompt("Ë°®Ê†º‰∏çËÉΩ‰∏∫Á©∫ÔºåËØ∑Ê£ÄÊü•", "--color", "red");
      }
    },
    AddEmoji() {
      this.emojiTurn = !this.emojiTurn;
    },
    KeyDown(e) {
      var event = e || window.event;
      var key = event.which || event.keyCode || event.charCode;
      if (key == 13) {
        this.OnSubmit();
      }
    },
    async GetMessageList() {
      let result = await this.$http.get("/message");
      this.messageList = result.data.reverse();
    },
  },
  mounted() {
    this.$store.dispatch('bulletin','ÊúâÈóÆÈ¢òÊ¨¢ËøéÁïôË®Ä„ÄÇ')
    document.addEventListener("keydown", this.KeyDown);
    this.GetMessageList();
  },
  beforeDestroy() {
    document.removeEventListener("keydown", this.KeyDown);
  },
};
</script>
<style lang="scss" scoped>
@media screen and (max-width: 520px) {
  #avatar {
    display: none !important;
  }
  .nick {
    width: calc(100% - 100px);
  }
}
#messageBox {
  width: 100%;
  padding: 15px;
  #messageContainer {
    width: 85%;
    margin: 0 auto;
    #header {
      height: 50px;
      width: 100%;
      align-content: center;
      display: flex;
      align-items: center;
      #avatar {
        display: inline-block;
        margin: 5px 10px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: burlywood;
        background-image: url("../assets/avatar2.jpg");
        background-size: cover;
        background-position: center;
        img {
          width: 100%;
          border-radius: 50%;
        }
      }
      .nick {
        margin: 0 5px;
        height: 30px;
        border: none;
        border-radius: 5px;
        background-color: #fdfdfd;
        &:focus {
          background-color: white;
          border: 1px rgb(23, 106, 139) solid;
          outline: none;
        }
      }
    }
    #textarea {
      width: 99%;
      height: 120px;
      resize: none;
      border: 0 steelblue solid;
      border-radius: 10px;
      outline: none;
      font-size: 16px;
      background-image: url(../assets/pinglun.png);
      background-position: center;
      background-size: cover;
      &:focus {
        background-color: rgb(255, 255, 255);
        border: rgb(56, 153, 192) 1px solid;
      }
    }
    #message {
      width: 100%;
      margin: 0 auto;
    }
    #foot {
      width: 100%;
      position: relative;
      #messagePrompt {
        --color: var(--blue3);
        display: inline-block;
        padding: 10px;
        background-color: var(--color);
        position: absolute;
        z-index: 9999;
        top: 80px;
        left: 50%;
        border-radius: 8px;
        transform: translateX(-50%);
        opacity: 0;
      }
      .addPromptAnimation {
        animation: Prompt 6s cubic-bezier(0.075, 0.52, 0.165, 1);
      }
      @keyframes Prompt {
        from {
          top: 80px;
          opacity: 1;
        }
        to {
          top: -80px;
          opacity: 0;
        }
      }
      #emojiArr {
        position: absolute;
        z-index: 999;
        width: 100%;
        padding: 4px;
        background-color: rgb(240, 240, 240);
        top: 40px;
        span {
          font-size: 21px;
          cursor: pointer;
        }
        transition: 1s;
      }
      #submitButton {
        display: inline-block;
        margin-right: 4px;
        position: absolute;
        right: 0;
      }
      #emoji {
        display: inline-block;
        font-size: 24px;
        &:hover {
          cursor: pointer;
          color: #66bfff;
        }
      }
    }
  }
  #messageListContainer {
    width: 100%;
    margin-top: 100px;
    padding-bottom: 20px;
    background-color: var(--blue1);
    #messageItem {
      display: flex;
      flex-wrap: nowrap;
      box-shadow: var(--shadow2);
      margin: 10px 0;
      background-color: var(--blue2);
      #messageAvatar {
        width: 40px;
        height: 40px;
        margin: 10px;
        img {
          width: 100%;
          border-radius: 20px;
        }
      }
      #messageItemContent {
        flex: 1;
        #messageItemHeader {
          display: flex;
          justify-content: space-between;
          margin: 10px;
          & > div {
            display: inline-block;
          }
        }
        #messageContent {
          margin: 10px 5px;
        }
        #replyItem {
          display: flex;
          flex-wrap: nowrap;
          margin: 10px 0;
          background-color: var(--blue2);
          margin: 5px;
          font-size: 14px;
          img {
            width: 35px;
          }
        }
      }
    }
  }
}
</style>